import { FacebookFilled, InstagramFilled, LinkedinFilled, TwitterSquareFilled } from '@ant-design/icons'
import { Button, Layout, message, Row, Space, Typography } from 'antd'
import React from 'react'
import { footer, header } from './data'
import './DefaultLayout.sass'
import Home from './Home'
import LeadCollectionForm from './LeadCollectionForm'

const { Header, Content, Footer } = Layout
const { Paragraph } = Typography

class DefaultLayout extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            visible: false
        }
    }

    onCreate = values => {
        // console.log('Received values of form: ', values)
        message.success('Thank you for showing interest. We will reach out to you shorty.')
        this.setState({
            visible: false
        })
    }

    render() {
        const { visible } = this.state
        return (
            <Layout>
                <Header>
                    <span className="logo">{header.logo}</span>
                    <Space size="large">
                        {
                            header.menu.map(item => (
                                <Button
                                    key={`menu-${item}`}
                                    style={{ color: "#fff" }}
                                    type="link"
                                >
                                    {item}
                                </Button>
                            ))
                        }
                        <Button
                            onClick={() => {
                                this.setState({
                                    visible: true
                                })
                            }}
                            shape="round"
                            type="primary"
                        >
                            {header.btn_title.toUpperCase()}
                        </Button>
                    </Space>
                </Header>
                <Content className="site-layout">
                    <Home />
                </Content>
                <Footer>
                    <Space align="center" direction="vertical" size="large">
                        <Row justify="center">
                            <Space>
                                <Button
                                    href="https://www.linkedin.com/company/grassetto-studio-s/about/"
                                    icon={<LinkedinFilled />}
                                    size="large"
                                    type="link"
                                />
                                <Button
                                    href="https://www.facebook.com/Grassetto-Studio-101339888281434/"
                                    icon={<FacebookFilled />}
                                    size="large"
                                    type="link"
                                />
                                <Button
                                    href="https://www.instagram.com/grassetto_studio/"
                                    icon={<InstagramFilled />}
                                    size="large"
                                    type="link"
                                />
                                <Button
                                    href="https://twitter.com/grassettos"
                                    icon={<TwitterSquareFilled />}
                                    size="large"
                                    type="link"
                                />
                            </Space>
                        </Row>
                        <Row justify="center">
                            <div className="logo">{footer.logo}</div>
                        </Row>
                        <Row justify="center">
                            <Paragraph>Â©2020 {footer.title}</Paragraph>
                        </Row>
                    </Space>
                </Footer>
                <LeadCollectionForm
                    visible={visible}
                    onCreate={this.onCreate}
                    onCancel={() => {
                        this.setState({
                            visible: false
                        })
                    }}
                />
            </Layout>
        )
    }
}

export default DefaultLayout